"use client";

import { MoreVertical } from "lucide-react";
import type { StatPillCardProps } from "./widget-types";
import { cn } from "@/lib/utils";
import { useEntityStateStore } from "@/stores/entity-state-store";
import { CARD_ICONS } from "./card-icons";

function useEntityValue(entityId: string) {
  const entity = useEntityStateStore((s) => s.getState(entityId));
  const raw = entity?.state;
  const unit = (entity?.attributes?.unit_of_measurement as string) ?? "";
  if (raw == null || raw === "unavailable" || raw === "unknown") {
    return { display: "—" };
  }
  const num = Number(raw);
  if (!Number.isNaN(num)) {
    const rounded = Math.round(num * 10) / 10;
    return { display: unit ? `${rounded} ${unit}` : String(rounded) };
  }
  return { display: String(raw) };
}

const PILL_CLASSES: Record<"amber" | "purple" | "emerald" | "red", string> = {
  amber:
    "border-amber-400/50 dark:border-amber-400/40 bg-amber-500/25 dark:bg-amber-900/30",
  purple:
    "border-purple-400/50 dark:border-purple-400/40 bg-purple-500/25 dark:bg-purple-900/30",
  emerald:
    "border-emerald-400/50 dark:border-emerald-400/40 bg-emerald-500/25 dark:bg-emerald-900/30",
  red: "border-red-400/50 dark:border-red-400/40 bg-red-500/25 dark:bg-red-900/30",
};

const ICON_CLASSES: Record<"amber" | "purple" | "emerald" | "red", string> = {
  amber: "text-amber-400",
  purple: "text-purple-300",
  emerald: "text-emerald-300",
  red: "text-red-300",
};

export function StatPillCardWidget({
  title = "Stat",
  entity_id,
  label,
  icon: iconName,
  color = "amber",
  size = "md",
  className,
  onMoreClick,
}: StatPillCardProps & { className?: string; onMoreClick?: () => void }) {
  const data = useEntityValue(entity_id);
  const IconComponent =
    iconName && CARD_ICONS[iconName] ? CARD_ICONS[iconName] : CARD_ICONS.Sun;
  const iconClassName = cn("h-3.5 w-3.5 shrink-0", ICON_CLASSES[color]);

  return (
    <div
      className={cn(
        "relative flex items-center justify-center p-4",
        size === "sm" && "text-sm",
        size === "md" && "text-base",
        size === "lg" && "text-lg",
        className
      )}
    >
      <div
        className={cn(
          "inline-flex items-center gap-2 rounded-full border backdrop-blur-xl shadow-lg px-3 py-1.5 text-white",
          PILL_CLASSES[color]
        )}
      >
        <IconComponent className={iconClassName} aria-hidden />
        <div className="flex flex-col items-start gap-0.5">
          <span className="text-xs font-bold tabular-nums leading-none text-white">
            {display}
          </span>
          <span className="text-[9px] font-medium leading-none text-white/80">
            {label ?? title}
          </span>
        </div>
      </div>
      {onMoreClick && (
        <button
          type="button"
          onClick={(e) => {
            e.stopPropagation();
            onMoreClick();
          }}
          className="absolute right-2 bottom-2 p-1.5 rounded-lg text-gray-500 hover:text-gray-700 hover:bg-white/10 transition-colors"
          aria-label="Opties"
        >
          <MoreVertical className="h-4 w-4" aria-hidden />
        </button>
      )}
    </div>
  );
}
